.notify-detail-msg{
	font-size: 0;
	padding-left: .2rem;

	.inner{
		position: relative;
	}

	.brand-logo{
		padding-bottom: .15rem;
	}

	.img-wraper{
		width: 1.28rem;
		height: auto;
		display: inline-block;
		vertical-align: middle;

		img{
			height: .29rem;
			width: auto;
			margin: 0 auto;
		}
	}	

	.act-tips{
		font-size: .17rem;
		max-width: 2.2rem;
		line-height: .23rem;
		color: #c81522;
		border: 1px solid #c81522;
		border-radius: .1rem;
		text-align: center;
		@include box-sizing(border-box);
		// margin-top: .03rem;
		margin-left: .07rem;
		@include text-overflow();
		padding: 0 .1rem;
		padding-top: .02rem;
		display: inline-block;
		vertical-align: middle;
	}

	.btn-start-remind{
		position: relative;

		i{
			position: absolute;
			left: .1rem;
			top: 50%;
			@include translate(0, -50%);
		}
	}

	.tick-wrap{
		width: 2.56rem;
		height: 100%;
		position: absolute;
		right: 0;
		top: 0;

		.line{
			position: absolute;
			left: 0;
			top: 50%;
			@include translate(0, -50%);
			width: 1px;
			height: 1.22rem;
			background: url(../images/notifyline.jpg) no-repeat 0 center;
			background-size: 1px 1.22rem; 
		}

		.tick-con{
			position: absolute;
			left: 50%;
			top: 50%;
			@include translate(-50%, -50%);

			p{
				font-size: .19rem;
				color: #949493;
				text-align: center;
				line-height: 1;
				padding-bottom: .17rem;
			}

			.time{
				font-size: .25rem;
				line-height: 1;
				white-space: nowrap;

				span{
					color: #c81522;
				}

				em{
					color: #949493;
				}
			}
		}
	}
}