/**
 * 活动预告商品列表
 */
.notifygds-list{
	@include clearfix();

	li{
		width: 50%;
		float: left;
		padding-right: .08rem;
		@include box-sizing(border-box);
		margin-bottom: .08rem;

		.gds-wrap{
			padding-bottom: .17rem;
		}

		.gds-img{
			// height: 3.072rem;
			position: relative;

			.icon-new-tag{
				position: absolute;
				left: .21rem;
				top: 0;
			}

			&.dry-up:after {
			    position: absolute;
			    width: 1rem;
			    height: 1rem;
			    text-align: center;
			    line-height: 1rem;
			    font-size: .24rem;
			    background: rgba(0,0,0,.6);
			    color: #fff;
			    left: 50%;
			    top: 50%;
			    -webkit-transform: translate(-50%,-50%);
			    content: '已抢光';
			    border-radius: 50%;
			}
		}

		.price{
			@include clearfix();
			padding: .15rem .18rem;
			line-height: 1;
			font-size: 0;

			.new{
				color: #c81522;
				padding-right: .11rem;
				font-size: .25rem;

				i{
					font-size: .17rem;
					font-style: normal;
					padding-right: .08rem;
				}
			}

			.old{
				font-size: .17rem;
				color: #6d6d6d;
			}

			.discount{
				width: .52rem;
				// height: .22rem;
				background-color: #c81522;
				border-radius: .03rem;
				font-size: .19rem;
				color: #fff;
				text-align: center;
				line-height: 1;
				padding: .02rem 0;
				padding-top: .03rem;
				opacity: 0;
			}
		}

		.name{
			color: #6d6d6d;
			font-size: .22rem;
			padding: 0 .18rem;
		    height: .52rem;
			display: -ms-box;
		    display: -webkit-box;
		    display: box;
		    -ms-line-clamp: 2;
		    -webkit-line-clamp: 2;
		    line-clamp: 2;
		    -ms-box-orient: vertical;
		    -webkit-box-orient: vertical;
		    box-orient: vertical;
		    overflow: hidden;
		    line-height: 1.3;
		}
	}
}