.notify-filter-slider{
	height: .63rem;
	@include clearfix();
	padding-right: .68rem;
	position: relative;
	overflow: hidden;

	li{
		float: left;
		width: auto;
		height: 100%;
		padding: 0 .26rem;

		&.active{
			.active-slider{
				display: block;
			}

			a{
				color: #c81522;
			}
		}

		a{
			display: block;
			height: 100%;
			line-height: .63rem;
			color: #6d6d6d;
			font-size: .22rem;
			position: relative;

			.active-slider{
				position: absolute;
			    height: 2px;
			    background: #c81522;
			    bottom: 0;
			    left: 0;
			    width: 100%;
			    display: none;
			}
		}
	}

	.filter{
		height: 100%;
		position: absolute;
		right: 0;
		top: 0;
		width: .68rem;
		line-height: .63rem;
		text-align: center;
		@include box-shadow(-.01rem 0 .8rem .1rem #f7f7f7);
		background: url(../images/notify-filter.png) no-repeat center center;
		background-size: .24rem .24rem;

		&.active{
			background: url(../images/notify-filter-active.png) no-repeat center center;
			background-size: .24rem .24rem;
		}
	}
}