.back-to-home{
	background-color: rgba(0, 0, 0, .76);
	position: fixed;
	right: 0;
	top: 50%;
	margin-top: -.28rem;
	z-index: 1000;
	border-top-left-radius: .28rem;
	border-bottom-left-radius: .28rem;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	padding-left: .6rem;
	padding-right: .1rem;
	padding-top: .20rem;
	padding-bottom: .19rem;

	i{
		position: absolute;
		left: .18rem;
		top: 50%;
		margin-top: -.14rem;
	}

	p{
		font-size: .18rem;
		color: #fff;
		line-height: 1;
	}
}